.date-picker {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1 1;
        flex: 1 1;
}

.month-picker, .date-picker {
    display: -ms-flexbox;
    display: flex;
}

.date-picker select, .date-picker input {
    font-size: 24px;
    margin: 5px;
    font-family: 'Amiri', serif;
}

.date-picker input {
    width: 70px;
    margin-right: 30px;
}

@media(max-width: 792px){
    .date-picker select, .date-picker input {
        font-size: 16px;
        margin: 5px;
        font-family: 'Amiri', serif;
    }

    .date-picker input {
        width: 50px;
        margin-right: 10px;
    }
}



